






























import { Component, Prop, Mixins } from 'vue-property-decorator';
import { Popover } from 'element-ui';
import { Node as ProsemirrorNode } from 'prosemirror-model';
import i18nMixin from '@/mixins/i18nMixin';
import { ImageDisplay } from '@/utils/image';
import CommandButton from '../CommandButton.vue';

@Component({
  components: {
    [Popover.name]: Popover,
    CommandButton,
  },
})
export default class ImageDisplayCommandButton extends Mixins(i18nMixin) {
  @Prop({
    type: ProsemirrorNode,
    required: true,
  })
  readonly node!: ProsemirrorNode;

  @Prop({
    type: Function,
    required: true,
  })
  readonly updateAttrs!: Function;

  popoverVisible = false;

  displayCollection = [
    ImageDisplay.INLINE,
    ImageDisplay.BREAK_TEXT,
    ImageDisplay.FLOAT_LEFT,
    ImageDisplay.FLOAT_RIGHT,
  ];

  private get currDisplay () {
    return this.node.attrs.display;
  }

  private hidePopover () {
    this.popoverVisible = false;
  }
}
