































































import { Component, Prop, Watch, Emit, Vue, Inject } from 'vue-property-decorator';
import { EditorStateOptions } from '@/../types';
import { Button, Popover, Input } from 'element-ui';
import CommandButton from './CommandButton.vue';

@Component({
  components: {
    [Button.name]: Button,
    [Popover.name]: Popover,
    [Input.name]: Input,
    CommandButton,
  },
})
export default class ColorPopover extends Vue {
  @Prop({
    type: Array,
    default: () => [],
  })
  readonly colorSet!: string[];

  @Prop({
    type: String,
    default: '',
  })
  readonly selectedColor!: string;

  @Prop({
    type: String,
    required: true,
  })
  readonly tooltip!: string;

  @Prop({
    type: String,
    required: true,
  })
  readonly icon!: string;

  @Prop({
    type: String,
    default: 'OK',
  })
  readonly confirmText!: string; // TODO: i18n ?

  private color: string = '';
  private popoverVisible: boolean = false;

  @Inject() readonly editorStateOptions!: EditorStateOptions;

  @Watch('selectedColor', {
    immediate: true,
  })
  onSelectedColorChange (color: string): void {
    this.color = color;
  }

  @Emit('confirm')
  confirmColor (color: string): string {
    this.popoverVisible = false;

    return color;
  }
};
